.App {
  font-size: 16px;
  text-align: center;
}

h1 {
  font-family: 'Special Elite', cursive;
  font-size: 3rem;
  font-weight: 100;
}

p {
  font-family: 'Nunito Sans', sans-serif;
  font-size: 1.5rem;
  font-weight: 200;
  padding: 0rem;
}

.App-catcher, .App-vinyl, .App-flower {
  height: 40vmin;
  pointer-events: none;
  position: absolute;
  top: 125px;
  margin-left: .2rem;
}

@media (prefers-reduced-motion: no-preference) {
  .App-catcher, .App-flower {
    animation: App-logo-spin infinite 10s linear;
  }
}

.App-catcher {
  height: 23vmin !important;
  margin-top: 60px;
  z-index: 3;
}

.App-vinyl {
  z-index: 2;
}

.App-flower {
  z-index: 1;
}

.text {
  margin-top: 20rem;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
